import React, { Children, useState } from "react";
import styles from "./section-fade-in.module.css";
import { Box, Card, Sheet, Stack, Typography } from "@mui/joy";
export function SectionFadeIn({ children, hasBackground = true }) {
  const arrayChildren = Children.toArray(children);

  const [activeElement, setActiveElement] = useState(arrayChildren[0]);

  return (
    <Box
      className="bg-blue py-5"
      sx={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
      }}
    >
      <Box
        sx={{
          display: "inline-flex",
          justifyContent: "start",
          gap: 2,
          py: 1,
          overflow: "auto",
          scrollSnapType: "x mandatory",
          "& > *": {
            scrollSnapAlign: "center",
          },
          "::-webkit-scrollbar": { display: "none" },
        }}
      >
        {arrayChildren.map((child, index) => {
          return (
            <div key={index} onClick={() => setActiveElement(child)}>
              {child}
            </div>
          );
        })}
      </Box>
      <Sheet
        variant="outlined"
        sx={{
          textAlign: "start",
          p: 3,
          borderRadius: "md",
          backgroundColor: "#071f28",
          color: "white",
        }}
      >
        <Typography level="body1" fontWeight="md">
          {activeElement.props.children}
        </Typography>
      </Sheet>
    </Box>
  );
}
