import React from "react";
import { Box, Sheet, Typography, Stack } from "@mui/joy";
import { useEffect, useRef } from "react";
export function Membership() {
  const iframeRef = useRef(null);
  useEffect(() => {
    const handleMessage = (e) => {
      // On filtre pour être sûr que ça vient bien de HelloAsso
      if (typeof e.data?.height === "number" && iframeRef.current) {
        const newHeight = e.data.height + "px";
        if (iframeRef.current.height !== newHeight) {
          iframeRef.current.height = newHeight;
        }
      }
    };

    window.addEventListener("message", handleMessage);
    return () => window.removeEventListener("message", handleMessage);
  }, []);
  return (
    <Box
      sx={{
        width: "100%",
        display: "flex",
        justifyContent: "center",
        py: { xs: 4, sm: 6 },
        px: { xs: 2, sm: 0 },
      }}
    >
      <Sheet
        variant="soft"
        sx={{
          width: { xs: "100%", sm: "80%" },
          display: "flex",
          flexDirection: { xs: "column", md: "row" },
          gap: 4,
          p: { xs: 2, sm: 5 },
          borderRadius: "xl",
          bgcolor: "#071f28",
          alignItems: "center",
        }}
      >
        <Box
          component="iframe"
          id="haWidget"
          allowTransparency="true"
          src="https://www.helloasso.com/associations/comput-yourself/adhesions/cotisation-pour-l-annee-2025-2026-comput-yourself/widget-vignette"
          title="HelloAsso Widget"
          sx={{
            width: { xs: "100%", sm: 350 },
            border: "none",
            borderRadius: "md",
          }}
          onLoad={() => {
            window.addEventListener("message", (e) => {
              const dataHeight = e.data.height;
              const haWidgetElement = document.getElementById("haWidget");
              if (haWidgetElement) haWidgetElement.height = dataHeight + "px";
            });
          }}
        />

        <Stack spacing={2} sx={{ flex: 1, alignItems: "center" }}>
          <Box sx={{ flex: 1 }}>
            <Typography level="h3" sx={{ mb: 2, color: "white" }}>
              Rejoins-nous pour l'année 2025-2026 !
            </Typography>
            <Typography sx={{ color: "white" }}>
              Pour adhérer à l'association et soutenir nos activités, tu peux
              utiliser les widgets HelloAsso ci-contre. Clique sur le bouton ou
              consulte la vignette pour finaliser ton adhésion.
            </Typography>
          </Box>
          <Box
            component="iframe"
            id="haWidgetButton"
            allowTransparency="true"
            src="https://www.helloasso.com/associations/comput-yourself/adhesions/cotisation-pour-l-annee-2025-2026-comput-yourself/widget-bouton"
            title="HelloAsso Button Widget"
            sx={{
              width: "100%",
              height: 70,
              border: "none",
              borderRadius: "md",
            }}
            onLoad={() => {
              window.addEventListener("message", (e) => {
                const dataHeight = e.data.height;
                const haWidgetElement = document.getElementById("haWidget");
                if (haWidgetElement) haWidgetElement.height = dataHeight + "px";
              });
            }}
          />
        </Stack>
      </Sheet>
    </Box>
  );
}
